import { useState } from "react"
import { DashboardLayout } from "@/components/dashboard/DashboardLayout"
import { EventSearch } from "@/components/events/EventSearch"
import { EventTable } from "@/components/events/EventTable"
import { Button } from "@/components/ui/button"  // assuming you use shadcn/ui Button
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Plus } from "lucide-react"  // optional icon

export const metadata = {
  title: "Events - Event Host",
  description: "Manage all events",
}

export default function EventsPage() {
  const [isOpen, setIsOpen] = useState(false)
  const [isEditMode, setIsEditMode] = useState(false)  // for future edit, currently always create

  // You can add form state here if needed, but for example keeping it simple

  return (
    <DashboardLayout>
      <div className="space-y-6">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-semibold text-foreground tracking-tight">Events</h1>
            <p className="text-muted-foreground mt-1">Manage and monitor all events</p>
          </div>

          <Dialog open={isOpen} onOpenChange={setIsOpen}>
            <DialogTrigger asChild>
              <Button>
                <Plus className="mr-2 h-4 w-4" />
                Add Event
              </Button>
            </DialogTrigger>

            <DialogContent className="max-w-3xl max-h-[90vh] overflow-y-auto">
              <DialogHeader>
                <DialogTitle>{isEditMode ? "Update Event" : "Create New Event"}</DialogTitle>
                <DialogDescription>
                  Fill in the details to {isEditMode ? "update" : "create"} your event.
                </DialogDescription>
              </DialogHeader>

              <div className="grid gap-6 py-4">
                {/* Basic Info */}
                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="eventName">Event Name *</Label>
                    <Input id="eventName" placeholder="Summer Music Festival" />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="hostedBy">Hosted By *</Label>
                    <Input id="hostedBy" placeholder="John Doe" />
                  </div>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="subtitle">Subtitle</Label>
                  <Input id="subtitle" placeholder="Optional subtitle" />
                </div>

                {/* Date & Time */}
                <div className="grid grid-cols-3 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="date">Date *</Label>
                    <Input id="date" type="date" />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="time">Time *</Label>
                    <Input id="time" type="time" />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="day">Day (optional)</Label>
                    <Input id="day" placeholder="Friday" />
                  </div>
                </div>

                {/* Location */}
                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="fullAddress">Full Address *</Label>
                    <Input id="fullAddress" placeholder="123 Main St" />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="city">City *</Label>
                    <Input id="city" placeholder="New York" />
                  </div>
                </div>

                {/* Descriptions */}
                <div className="space-y-2">
                  <Label htmlFor="about">About</Label>
                  <Textarea id="about" rows={4} placeholder="Describe your event..." />
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="partyFlow">Party Flow</Label>
                    <Textarea id="partyFlow" />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="partyEtiquette">Party Etiquette</Label>
                    <Textarea id="partyEtiquette" />
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="whatsIncluded">Whats Included</Label>
                    <Textarea id="whatsIncluded" />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="houseRules">House Rules</Label>
                    <Textarea id="houseRules" />
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="howItWorks">How It Works</Label>
                    <Textarea id="howItWorks" />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="cancellationPolicy">Cancellation Policy</Label>
                    <Textarea id="cancellationPolicy" />
                  </div>
                </div>

                {/* Additional */}
                <div className="grid grid-cols-3 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="ageRestriction">Age Restriction</Label>
                    <Input id="ageRestriction" placeholder="21+" />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="category">Category</Label>
                    <Input id="category" placeholder="Music, Conference, etc." />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="maxCapacity">Max Capacity</Label>
                    <Input id="maxCapacity" type="number" />
                  </div>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="eventImage">Event Image URL (optional)</Label>
                  <Input id="eventImage" placeholder="https://example.com/image.jpg" />
                </div>

                {/* You can add more fields like passes array, location coordinates, etc. if needed */}
                {/* For passes, you might need a more complex repeater component */}

              </div>

              <DialogFooter>
                <Button variant="outline" onClick={() => setIsOpen(false)}>
                  Cancel
                </Button>
                {isEditMode ? (
                  <Button type="submit">Update Event</Button>
                ) : (
                  <Button type="submit">Create Event</Button>
                )}
              </DialogFooter>
            </DialogContent>
          </Dialog>
        </div>

        <EventSearch />
        <EventTable />
      </div>
    </DashboardLayout>
  )
}